<template>
    <div class="comment-dialog">
        <md-dialog :md-active.sync="showDialog">
            <md-dialog-title>
                <div class="title-row">
                    <div>
                        {{$t('comment_title')}}
                    </div>
                    <md-button class="md-icon-button" @click="showDialog = false">
                        <md-icon>close</md-icon>
                    </md-button>
                </div>
            </md-dialog-title>

            <md-dialog-content>
                <span class="md-body-2" v-html="$t('comment_msg')">
                </span>
                <form novalidate>
                    <md-field>
                        <md-textarea v-model="comment"></md-textarea>
                    </md-field>
                </form>
            </md-dialog-content>

            <md-dialog-actions>
                <md-button class="md-primary" @click="onSubmitSend()" :disabled="sending || !isValidatedComment">
                    {{$t('send')}}
                </md-button>
            </md-dialog-actions>
        </md-dialog>
        <md-snackbar :md-position="'center'" :md-duration="1300"
                     :md-active.sync="showSnackbar" md-persistent>
            <span>
                {{$t('success_comment_msg')}}
            </span>
        </md-snackbar>
    </div>

</template>

<script>
    import db from "../utils/firestore"

    export default {
        name: "CommentDialog",
        data: () => ({
            showDialog: false,
            showSnackbar: false,
            comment: null,
            isValidatedComment: false,
            sending: false,
        }),
        watch: {
            comment(value) {
                this.comment = value;
                this._validateComment(value);
            },
        },
        methods: {
            _validateComment(value) {
                this.isValidatedComment = value != null ? value.length > 3 : false;
            },
            open() {
                this.showDialog = true;
            },
            onSubmitSend() {
                db.collection("comment_to_mantman")
                    .add({
                        comment: this.comment,
                        created: Date.now(),
                        is_check: false,
                        is_feedback: false
                    })
                    .then(() => {
                        this.showSnackbar = true;
                        this.showDialog = false;
                        this.comment = null;
                    })
                    .catch((error) => {
                        alert('Error TT');
                        console.error("Error writing document: ", error);
                    });
            }
        }
    }
</script>

<style scoped>
    .title-row {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }
</style>
