<template>
    <div class="subscribe-mantman">
        <form novalidate @submit.prevent="onSubmitSubscribe">
            <span class="md-subheading">
                {{$t('subscribe_msg')}}
            </span>
            <md-field>
                <label for="email">{{$t('email')}}</label>
                <md-input type="email" name="email" id="email"
                          autocomplete="email" v-model="email" :disabled="sending"/>
            </md-field>
            <div class="subscribe-btn-container">
                <md-button type="submit" class="md-raised md-primary subscribe-btn"
                           :disabled="sending || !isValidatedEmail">
                    {{$t('subscribe')}}
                </md-button>
            </div>
        </form>

        <md-snackbar :md-position="'center'" :md-duration="1500"
                     :md-active.sync="showSnackbar" md-persistent>
            <span>
                {{$t('success_subscribe_msg')}}
            </span>
        </md-snackbar>
    </div>
</template>

<script>
    import db from "../utils/firestore"

    export default {
        name: "SubscribeMantman",
        data: () => ({
            email: null,
            isValidatedEmail: false,
            sending: false,
            showSnackbar: false
        }),
        watch: {
            email(value) {
                this.email = value;
                this._validateEmail(value);
            }
        },
        methods: {
            _validateEmail(value) {
                this.isValidatedEmail = (/^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(value));
            },
            _subscribeToMantman() {
                db.collection("subscribe_to_mantman")
                    .add({
                        email: this.email,
                        created: Date.now(),
                        is_validate: false,
                        un_subscribe: false
                    })
                    .then(() => {
                        this.email = null;
                        this.isValidatedEmail = false;
                        this.sending = false;
                        this.showSnackbar = true;
                    })
                    .catch((error) => {
                        alert('Error TT');
                        console.error("Error writing document: ", error);
                    });
            },
            onSubmitSubscribe() {
                if (this.isValidatedEmail) {
                    this.sending = true;
                    this._subscribeToMantman();
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    .subscribe-btn-container {
        text-align: center;
    }

    .subscribe-btn {
        /*text-align: right;*/
        width: 90%;
        /*margin: 0 auto;*/
    }
</style>
